nav {
  width: 100vw;
  height: 8rem;
  display: grid;
  place-items: center;
  background-color: var(--color-primary);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  transition: var(--transition);
  box-shadow: 2px 1px 12px 3px #553944
}

.container.nav-container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-logo {
  width: 20rem;
}

.nav-menu {
  display: flex;
  gap: 4rem;
}

.nav-menu li {
  font-size: 180%;
}

.nav-menu a.active,
.nav-menu a:active,
.nav-menu a:hover {
  color: #8a8a8a;
}

@media screen and (max-width: 1024px) {
  .nav-menu {
    display: none;
  }
}
